<template>
  <div class="index">
	  <div v-for="(item, key) in newsList">
	  	<news-cell
	  	:newsDate="item"
	  	:key="key"
	  	></news-cell>
	  </div>
  </div>
</template>

<script>
import api from './../axios/api.js'
import NewsCell from './../components/index/NewsCell.vue'

export default {
  name: 'index',
  data () {
    return {
    	type: {
    		'头条': 'top',
    		'社会': 'shehui',
    		'国内': 'guonei',
    		'国际': 'guoji',
    		'娱乐': 'yule',
    		'体育': 'tiyu',
    		'军事': 'junshi',
    		'科技': 'keji',
    		'财经': 'caijing',
    		'时尚': 'shishang'
    	},
    	newsList: []
    }
  },
  components: {
  	NewsCell
  },
  created() {
  	api.JH_NewsApi('type=junshi')
  	.then(res => {
  		console.log(res);
  		this.newsList = res.result.data;
  	});
  	api.JB_jokeApi('https://bird.ioliu.cn/joke/', {type: 'text', page: 1, pagesize: 10})
  	.then( res => {
  		console.log(res)
  	})
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



